<template>
<div class=" w90p pt48 pb48" style="margin:0 auto;">
    <div class="firstTitle ">控排企业供需报价</div>
    <div class="secondDesc">控排企业供需报价简介</div>
    <div class="flexBox mt24" style="">
        <div class="w50p" style="">
            <div class="p16" style="background-color: rgb(30, 32, 38);">
                <Input v-model="page.name" search  class="search" placeholder="请输入能源或化工品种" style="width:250px;" @on-search="search" />
                <div class="mt20" @scroll="scrollEvent">
                    <!-- <div v-for="(v,o) in list" :key="o" class="list">
                        <div style="flex:1;">二甘醇</div>
                        <div class="ml20">价格指数：<span>89.34</span></div>
                    </div> -->
                    <!-- <div v-for="(v,o) in list" :key="o" class="list">
                        <div style="flex:1;">{{ v.name }}</div>
                        <div class="ml10">{{ v.contacts }}</div>
                        <div class="ml10">{{ v.mobile }}</div>
                        <div class="ml10">
                            <span>{{ v.price | toFixed(2) }}</span>
                        </div>
                    </div> -->
                    <table class="defineTable mt20 dataList " cellspacing="0">
                        <thead>
                            <tr class="">
                                <!-- <td class="tleft">品种代码</td> -->
                                <td class="">品种</td>
                                <td class="">供/需</td>
                                <td class="">单价</td>
                                <td class="">数量</td>
                                <td class="">姓名</td>
                                <td class="">手机号</td>
                                <!-- <td class="">发布时间</td> -->
                                
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="(v,o) in list" :key="o">
                                <td class="">{{ v.name }}</td>
                                <td :class="v.in=='B'?'green':'red'">{{ v.in=='B'?'买':'卖' }}</td>
                                <td>
                                    <!-- 价格指数： -->
                                    <span>{{ v.price | toFixed(2) }}</span>
                                </td>
                                <td class="">{{ v.amount }}</td>
                                <td class="">{{ v.contacts }}</td>
                                <td class="">{{ v.mobile }}</td>
                                <!-- <td class="">{{ v.push }}</td> -->
                            </tr>
                            <tr v-if="list.length==0">
                                <td colspan="6" class="center cloc">暂无数据</td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
        <div class="p16" style="flex:1;margin-left:40px;background-color: rgb(30, 32, 38);">
            <!-- <div>报价</div> -->
            <Form :model="form" :label-width="120">
                <FormItem label="发布时间">
                    <DatePicker type="date" placeholder="请选择发布时间" v-model="form.push"></DatePicker>
                </FormItem>
                <FormItem label="能源、化工品种">
                    <Input v-model="form.name" placeholder="请输入品种名称" class="w300"></Input>
                </FormItem>
                <FormItem label="供/需">
                    <Select v-model="form.in" class="w300" placeholder="请选择供需方向">
                        <Option value="S">供</Option>
                        <Option value="B">需</Option>
                    </Select>
                </FormItem>
                <FormItem label="品种单价">
                    <Input v-model="form.price" type="number" placeholder="请输入品种单价" class="w300"></Input>
                </FormItem>
                <FormItem label="品种数量">
                    <Input v-model="form.amount" type="number" placeholder="请输入品种数量" class="w300"></Input>
                </FormItem>
                <FormItem label="有效期">
                    <DatePicker type="date" placeholder="开始时间" v-model="form.startTime"></DatePicker>
                    <span class="ml5 mr5">-</span>
                    <DatePicker type="date" placeholder="结束时间" v-model="form.endTime"></DatePicker>
                </FormItem>
                <FormItem label="联系人姓名">
                    <Input v-model="form.contacts" placeholder="请输入联系人姓名" class="w300"></Input>
                </FormItem>
                <FormItem label="联系人手机号">
                    <Input v-model="form.mobile" type="number" placeholder="请输入联系人手机号" class="w300"></Input>
                </FormItem>
                <FormItem>
                    <Button type="primary" style="min-width:120px;" @click="submit">提交</Button>
                    <Button style="margin-left: 15px;min-width:120px;" @click="form={}">重置</Button>
                </FormItem>
            </Form>
        </div>
    </div>
</div>
</template>

<script>
export default {
    name: '',
    props: {
    },
    components: {

    },
    data () {
        return {
            form: {
                // input: '',
                // select: '',
                // radio: 'male',
                // checkbox: [],
                // switch: true,
                // date: '',
                // time: '',
                // slider: [20, 50],
                // textarea: ''
                "amount": '',
                "contacts": "",
                // "endTime": '',
                // "in": false,
                "mobile": "",
                "name": "",
                "price": '',
                // "push": '',
                // "startTime": ''
            },
            list:[],
            page:{
                "current": 1,
                "size": 20,
                loading:false,
                finish:false,
                name:''
            },
        }
    },
    mounted() {
        this.getList();
    },
    methods: {
        search(){
            this.page.current=1;
            this.getList();
        },
        //提交
        submit(){
            let d={
                // "amount": 0,
                // "contacts": "string",
                // "endTime": 0,
                // "in": false,
                // "mobile": "string",
                // "name": "string",
                // "price": 0,
                // "push": 0,
                // "startTime": 0
                // B buy  S sell，买入卖出类型。
                ...this.form,
                startTime:this.$dayjs(this.form.startTime).format('YYYY-MM-DD HH:mm:ss'),
                endTime:this.$dayjs(this.form.endTime).format('YYYY-MM-DD HH:mm:ss'),
                push:this.$dayjs(this.form.push).format('YYYY-MM-DD HH:mm:ss'),
                type:'enterprise',//enterprise：企业，mechanism：机构
            }
            if(!d.mobile){
                this.$Message.error('请输入联系人手机号!');
                return;
            }
            this.$api.offer_apply(d).then(res=>{
                this.$Message.success('提交成功!');
            });
        },
        // 滚定监听
        scrollEvent(e) {
            let that=this;
            if (Math.ceil(e.currentTarget.scrollTop + e.currentTarget.clientHeight) >=e.currentTarget.scrollHeight-20) {   //容差：20px
                console.log('滚动到底部');
                if(!that.page.finish && !that.page.loading){
                    that.page.current+=1;
                    that.getList();
                }
            }
        },
        //列表
        async getList(){
            if(this.page.loading){
                return;
            }
            let d={
                // area: this.area.type,
                current: this.page.current,
                size: this.page.size,
                name:this.page.name,
                type:'enterprise',//enterprise：企业，mechanism：机构
            };
            this.page.loading=true;
            let res = await this.$api.offer_page(d);
            let list = res.list || [];
            if (list.length >= d.size) {
                this.page.finish = false;
            } else {
                this.page.finish = true;
            }
            if(d.current==1){
                this.list=list;
            }else{
                this.list=this.list.concat(list);
            }
            this.page.loading=false;
        },
    },
}
</script>

<style scoped lang='less'>
.search :deep(.ivu-input){
    // padding:8px 12px;
    height: 40px;
    line-height: 40px;
}
.search :deep(.ivu-input-icon){
    line-height: 40px;
}
.pannel{
    background: #222;
    padding:20px;
    border-radius: 8px;
}
.list{
    display: flex;
    padding:8px 0;
    border-bottom: 1px solid #434651;
    opacity: 0.8;
}
</style>